body {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25vw;
  line-height: 1.5;
  color: #0B1D26;
}

img {
  max-width: 100%;
  height: auto;
}

.gu-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  background: #fff;
}
@media screen and (min-width: 768px) {
  .gu-header {
    height: 5.73vw;
    padding: 0 4.17vw 0.52vw 2.5vw;
  }
}
@media screen and (max-width: 767px) {
  .gu-header {
    height: 10.18vw;
    padding: 4.07vw 4.07vw 0;
  }
}
@media screen and (min-width: 768px) {
  .gu-header__logo {
    width: 13.02vw;
    padding: 0 0 0.42vw;
  }
}
@media screen and (max-width: 767px) {
  .gu-header__logo {
    width: 29.01vw;
    padding: 0 0;
  }
  .gu-header__logo img {
    vertical-align: baseline;
  }
}
.gu-header__navList {
  display: flex;
  justify-content: space-between;
}
.gu-header__navList li {
  width: 11.67vw;
}
.gu-header__navList li a {
  margin: 0 auto;
  display: block;
  width: fit-content;
  text-align: center;
  padding: 0.83vw 0 0.42vw;
}
@media screen and (max-width: 767px) {
  .gu-header__navList {
    display: none;
  }
}

.gu-mobile-menu-icon {
  position: absolute;
  top: 4.07vw;
  right: 4.07vw;
  z-index: 20;
}
@media screen and (min-width: 768px) {
  .gu-mobile-menu-icon {
    display: none;
  }
}
.gu-mobile-menu-icon > span {
  background-color: black;
  width: 6.11vw;
  height: 1px;
  display: block;
  margin-bottom: 2.04vw;
  transition: transform 0.7s;
}
.gu-mobile-menu-icon > span:last-child {
  margin-bottom: 0;
}
.gu-mobile-menu-icon.js-open > span {
  background: #fff;
}
.gu-mobile-menu-icon.js-open > span:nth-child(1) {
  transition-delay: 70ms;
  transform: translateY(2.29vw) rotate(135deg);
}
.gu-mobile-menu-icon.js-open > span:nth-child(2) {
  transition-delay: 0s;
  transform: translateX(-4.07vw) scaleX(0);
}
.gu-mobile-menu-icon.js-open > span:nth-child(3) {
  transition-delay: 140ms;
  transform: translateY(-2.29vw) rotate(-135deg);
}

body.js-overlay {
  overflow-y: hidden;
}

.gu-overlay {
  color: #fff;
  position: absolute;
  height: 100vh;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: #0B5773;
  padding: 3.82vh 3.82vh 6.37vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in;
  z-index: 18;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.gu-overlay.js-show {
  opacity: 1;
  pointer-events: auto;
}
@media screen and (min-width: 768px) {
  .gu-overlay {
    display: none;
  }
}
.gu-overlay__header {
  margin-top: 8.92vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.gu-overlay__headerLogo {
  width: 17.83vh;
  margin-bottom: 2.23vh;
}
.gu-overlay__headerAddress {
  font-weight: 400;
  font-size: 1.27vh;
  margin-bottom: 3.03vh;
}
.gu-overlay__headerSnsIcons {
  display: flex;
  gap: 2.55vh;
}
.gu-overlay__headerSnsIcons li {
  width: 2.55vh;
}
.gu-overlay__navList {
  display: flex;
  flex-direction: column;
  font-size: 2.23vh;
  gap: 4.78vh;
}
.gu-overlay__copyright {
  font-family: roboto;
  font-size: 1.91vh;
  font-weight: 300;
}

.gu-footer {
  color: #fff;
  background: #0B5773;
  font-weight: 400;
  position: relative;
}
@media screen and (min-width: 768px) {
  .gu-footer {
    font-size: 0.94vw;
  }
}
@media screen and (max-width: 767px) {
  .gu-footer {
    font-size: 4.07vw;
  }
}
.gu-footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (min-width: 768px) {
  .gu-footer__container {
    padding: 2.86vw 0 3.13vw;
    max-width: 77.08vw;
    margin: 0 auto;
  }
}
@media screen and (max-width: 767px) {
  .gu-footer__container {
    flex-direction: column;
    padding: 13.23vw 9.16vw 38.17vw;
  }
}
.gu-footer__addressArea {
  width: 15.63vw;
}
@media screen and (max-width: 767px) {
  .gu-footer__addressArea {
    text-align: center;
    width: 100%;
  }
}
@media screen and (min-width: 768px) {
  .gu-footer__logo {
    width: 13.02vw;
    margin-bottom: 2.08vw;
  }
}
@media screen and (max-width: 767px) {
  .gu-footer__logo {
    width: 28.5vw;
    margin: 0 auto 4.07vw;
  }
}
.gu-footer__address {
  margin-bottom: 2.08vw;
}
@media screen and (max-width: 767px) {
  .gu-footer__address {
    font-size: 2.04vw;
    margin-bottom: 12.72vw;
  }
}
@media screen and (max-width: 767px) {
  .gu-footer__copyright {
    font-size: 3.05vw;
    position: absolute;
    bottom: 10.18vw;
    left: 50%;
    transform: translateX(-50%);
  }
}
.gu-footer__mapWrap {
  max-width: 31.25vw;
  width: 31.25vw;
}
@media screen and (max-width: 767px) {
  .gu-footer__mapWrap {
    max-width: 100%;
    width: 100%;
  }
}
.gu-footer__map {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 50%; /* 比率を2:1に固定 */
}
@media screen and (max-width: 767px) {
  .gu-footer__map {
    padding-top: 62.5%;
    margin-bottom: 12.72vw;
  }
}
.gu-footer__map iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media screen and (min-width: 768px) {
  .gu-footer__nav {
    width: 15.63vw;
  }
}
.gu-footer__navList {
  display: flex;
  gap: 0.89vw;
  flex-direction: column;
}
@media screen and (max-width: 767px) {
  .gu-footer__navList {
    gap: 7.63vw;
  }
}

